import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import GridListTileBar from "@material-ui/core/GridListTileBar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import InfoIcon from "@material-ui/icons/Info";
import tileData from "../../data.js";
import Link from "next/link";

const useStyles = theme => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-around",
    overflow: "hidden",
    backgroundColor: theme.palette.background.paper
  },
  gridList: {
    width: 1440,
    height: 1050
  },
  icon: {
    color: "rgba(255, 255, 255, 0.54)"
  }
});

function RecipeReviewCard(props) {

  const { classes } = props;
  
  function onTileTouch(ev){
      window.location = "/post?postId=" + (ev + 1)
  }
  return (
    <React.Fragment>
      <div className={classes.root}>
        <GridList className={classes.gridList} cols={6}>
          {tileData.map((tile, id) => (
            <GridListTile onClick={ (e) => {
                onTileTouch(id);
              }} key={id.toString()} rows={2} cols={2}>
              <img src={tile.img} alt={tile.title} />
              <GridListTileBar
                title={tile.title}
                subtitle={<span>by: {tile.author}</span>}
                actionIcon={
                  <IconButton
                    aria-label={`info about ${tile.title}`}
                    className={classes.icon}>
                    <InfoIcon />
                  </IconButton>
                }
              >
              </GridListTileBar>
              <Link href={{ pathname: "/post", query: { postId: id } }}>
                  <Typography variant="subtitle1" color="primary">
                    Continue reading...
                  </Typography>
                </Link>
            </GridListTile>
          ))}
        </GridList>
      </div>
    </React.Fragment>
  );
}

RecipeReviewCard.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(useStyles)(RecipeReviewCard);